<!--  -->
<template>
  <div>
    <el-dialog title="" :visible.sync="data.show">
      <div>
        <span>
          .zip
        </span>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-upload
          class="avatar-uploader"
          :action="importimage"
          :with-credentials="true"
          :show-file-list="false"
          :on-progress="uploadProgress"
          :on-success="uploadCb">
          <el-button type="primary"></el-button>
        </el-upload>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Config from '../../config/common.json'

export default {
  name: 'ImportImage',
  components: {
    
  },
  props: {
    data: {
      type: Object
    }
  },
  data() {
    return {
      importimage: Config.server + '/user/importimage',
    }
  },
  computed: {
    
  },
  methods: {
    // 
    uploadProgress(event) {
      console.log(event)
      this.$emit('uploadProgress')
      this.$message({
        showClose: true,
        message: '',
        type: 'warning',
        duration: 0
      });
    },
    // 
    uploadCb(res) {
      this.$message.closeAll()
      console.log(res)
      this.$emit('uploadCb')
      if (res.success) {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.data.show = false
        this.$emit('success')
      } else {
        this.$message({
          showClose: true,
          message: res.message,
          type: 'error'
        }); 
      }
    },
  },
  mounted() {
    
  }
}
</script>

