import Vue from 'vue/dist/vue.esm.js'
import VueRouter from 'vue-router'

import login from '../pages/login.vue'
import error from '../pages/error.vue'
import unit from '../pages/unit/unitlist.vue'
import user from '../pages/user/userlist.vue'
import employeecard from '../pages/expiration/employeecard.vue'
import chartlist from '../pages/chart/chartlist.vue'
import authority from '../pages/system/authority.vue'
import role from '../pages/system/role.vue'
import role_user_bind from '../pages/system/role_user_bind.vue'
import wages from '../pages/wages/manage.vue'
import epidemic_list from '../pages/epidemic/list.vue'
import epidemic_collect from '../pages/epidemic/collect.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/UserManager/UserList' },
  { path: '/error', component: error }, // 404
  { path: '/login', component: login }, // 
  { path: '/CompanyManager/CompanyList', component: unit }, // 
  { path: '/UserManager/UserList', component: user }, // 
  { path: '/ChartManger/ChartList', component: chartlist }, // 
  { path: '/SystemManager/AuthorityManager', component: authority }, // 
  { path: '/SystemManager/RoleManager', component: role }, // 
  { path: '/SystemManager/RoleUserBindManager', component: role_user_bind }, // 
  { path: '/EpidemicManager/EpidemicListManager', component: epidemic_list }, // 
  { path: '/EpidemicManager/EpidemicCollectManager', component: epidemic_collect }, // 
  // 
  { path: '/ExpireManager/ExpireEmployeeCard', component: employeecard }, // 
  { path: '/ExpireManager/ExpireContract', component: employeecard }, // 
  { path: '/ExpireManager/ExpireRetire', component: employeecard }, // 
  { path: '/BaseDataStatistics/YearStatistics', component: employeecard }, // 
  { path: '/BaseDataStatistics/MonthStatistics', component: employeecard }, // 
  { path: '/WagesManager/WagesDetail', component: wages }, // 
]

const router = new VueRouter({
  // mode: 'hash',
  mode: 'history',
  routes
})

export default router
