<template>
  <div>
    <div>
      <el-row>
        <el-col :span="12">
          <el-button type="primary" size="mini" @click="currUnit = '';dialogData.show = true"></el-button>
        </el-col>
      </el-row>
    </div>
    <Tree :data="unitList" :operate="operate" :expandAll="true" @operateClick="operateClick"></Tree>
    <Dialog :data="dialogData" @success="dialogSuccess"></Dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import Tree from '../../components/tree.vue'
import Dialog from '../../components/dialog/index.vue'

export default {
  name: 'Unit',
  components: {
    Tree,
    Dialog
  },
  computed: {
    ...mapState({
      unitList: 'unit',
    }),
  },
  data() {
    return {
      dialogData: {
        title: '',
        show: false,
        form: [
          { label: '', value: '', placeholder: '' },
          { label: '', value: '', type: "textarea" }
        ]
      },
      operate: [
        { title: '', type: 'detail' },
        { title: '', type: 'add' },
        { title: '', type: 'del' }
      ],
      currUnit: {} // 
    }
  },
  methods: {
    ...mapActions([
      'unit'
    ]),
    // 
    dialogSuccess() {
      console.log('dialogSuccess', this.dialogData.form)
      this.addUnit()
    },
    operateClick(opt) {
      console.log(opt)
      this.currUnit = opt.data || {}
      switch(opt.type) {
        case 'detail': {
          this.dialogData = {
            title: '',
            show: true,
            hideOperate: true,
            form: [
              { label: '', value: opt.data.name, placeholder: '' },
              { label: '', value: opt.data.description, type: "textarea" }
            ]
          }
          break
        }
        case 'add': {
          console.log('addd')
          this.dialogData = {
            title: '',
            show: true,
            form: [
              { label: '', value: '', placeholder: '' },
              { label: '', value: '', type: "textarea" },
              { label: '', value: this.currUnit.name, type: 'span'  },
            ]
          }
          break
        }
        case 'del': {
          this.$confirm(' ' + this.currUnit.name + ' ?').then(res => {
            if (res == 'confirm') this.delUnit()
          }).catch(err => {
            console.log(err)
          });
          break
        }
      }
    },
    // 
    addUnit() {
      this.request.post('/company/add', {
        name: this.dialogData.form[0].value,
        desc: this.dialogData.form[1].value,
        fatherCode: this.currUnit.code
      }).then(res => {
        if (res.status == 200 & res.data.success) {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
          this.unit()
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      }) 
    },
    // 
    delUnit() {
      this.request.post('/company/delete', {
        code: this.currUnit.code
      }).then(res => {
        if (res.status == 200 & res.data.success) {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
          this.unit()
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      }) 
    }
  },
  mounted() {
    this.unit()
  }
}
</script>

